html {
  font-size: 16px; }

body {
  font-family: "Open Sans","Helvetica Neue",Helvetica,Arial,sans-serif;
  background: no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  -o-background-size: cover; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Raleway","Helvetica Neue",Helvetica,Arial,sans-serif;
  font-weight: 500;
  letter-spacing: 1px; }

h1 {
  text-transform: uppercase; }

h2 {
  margin: 0; }

p {
  font-size: 1rem;
  line-height: 1.6;
  color: #000;
  margin: 0; }

hr {
  max-width: 400px;
  border-color: #999999; }

img {
  height: auto;
  width: 100%; }

/* Text meant only for screen readers. */
.sr-only {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important; }

.wrapper {
  margin-right: auto;
  margin-left: auto;
  width: 100%; }
  .wrapper section {
    background-color: rgba(255, 255, 255, 0.9);
    margin: 5rem 0;
    padding: 1rem 0; }
    .wrapper section .col {
      padding: 1rem; }
  .wrapper .row {
    display: flex; }
  .wrapper .one_third,
  .wrapper .one_fourth {
    flex: 1; }
  .wrapper .two_third {
    flex: 2; }
  .wrapper .three_fourths {
    flex: 3; }
  .wrapper .last {
    margin-right: 0; }

@media (min-width: 600px) {
  .wrapper {
    max-width: 540px; } }
@media (min-width: 800px) {
  .wrapper {
    max-width: 720px; } }
@media (min-width: 1000px) {
  .wrapper {
    max-width: 960px; } }
@media (min-width: 1200px) {
  .wrapper {
    max-width: 1140px; } }
img {
  height: auto;
  max-width: 100%; }

h1.site-title {
  margin: 0;
  text-align: center; }
  h1.site-title a {
    color: #fff;
    font-size: 4rem;
    font-weight: 700;
    letter-spacing: 3px;
    margin: 0;
    padding: 3em 0 1em;
    text-decoration: none;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
    text-transform: none; }

p.site-description {
  color: #fff;
  font-size: 1.25rem;
  font-weight: 400;
  letter-spacing: 3px;
  margin: 0;
  padding: .5rem 0 2rem;
  text-align: center;
  text-decoration: none;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  text-transform: uppercase; }

.main-navigation {
  left: 0px;
  margin-bottom: 2rem;
  position: static;
  right: 0px;
  top: 0px;
  width: 100%;
  z-index: 999; }

.menu-main-menu-container {
  background-color: #fff; }
  .menu-main-menu-container .menu {
    align-items: center;
    display: flex;
    list-style: none;
    justify-content: center;
    margin: 0 auto;
    padding: 0; }
    .menu-main-menu-container .menu .menu-item {
      padding: 2rem; }
      .menu-main-menu-container .menu .menu-item a {
        color: #777;
        font-size: 1.25rem;
        font-weight: 400;
        text-decoration: none; }
        .menu-main-menu-container .menu .menu-item a:hover {
          color: #333; }

.hero-slider {
  position: relative; }
  .hero-slider .slick-dots {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    z-index: 15;
    width: 60%;
    padding-left: 0;
    margin-left: -30%;
    text-align: center;
    list-style: none; }
    .hero-slider .slick-dots li {
      display: inline-block;
      width: 10px;
      height: 10px;
      margin: 1px 3px;
      text-indent: -999px;
      cursor: pointer;
      background-color: #000 \9;
      background-color: rgba(0, 0, 0, 0);
      border: 1px solid #fff;
      border-radius: 10px;
      box-sizing: border-box; }
      .hero-slider .slick-dots li.slick-active {
        background-color: #fff; }
      .hero-slider .slick-dots li button {
        display: none; }
